import React, { useEffect, useState } from "react";
import { makeStyles } from '@material-ui/core/styles';
import { Paper } from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import Layout from 'layout/Layout';
import { GetSiteConfig, GetSiteConfigNew } from "services/BlogService";
import { Images } from 'configs/SiteConfig';
import { LivePageMetadata } from 'configs/PageMetadata';
import Metadata from 'components/Metadata';

const useStyles = makeStyles(() => ({
    root: {
        margin: 20,
        padding: 20,
        height: "80vh",
    },
    rootNoLive: {
        margin: 20,
        padding: 20,
    },
    sectionTitle: {
        paddingBottom: 10
    },
    videoContainer: {
        margin: 20,
        padding: 20,
        position: "relative",
        paddingBottom: "53%",
        height: "0"
    },
    video: {
        position: "absolute",
        top: 0,
        left: 0,
        width: "95%",
        height: "95%"
    },
    img: {
        maxWidth: "560px",
        width: "100%"
    }
}));

export default function LivePage() {
    const classes = useStyles();
    const [ytLiveUrlKey, setYTLiveUrlKey] = useState(null);

    useEffect(() => {
        GetSiteConfigNew()
            .then((response) => {
                if (response !== undefined) {
                    console.log(response.value);
                    var result = response.value.find(t => t.RowKey === 'YTLiveUrlKey').value;
                    setYTLiveUrlKey(result);
                }
            });
    }, []);

    return (
        <Layout>
            { !ytLiveUrlKey ?
                <Paper elevation={3} className={classes.rootNoLive}>
                    <Typography variant="h6" className={classes.sectionTitle}>
                        Bipul is not Livestreaming right now.
                    </Typography>
                    <br /><br />
                    <img src={Images.NotLive} className={classes.img} alt="Bipul is not Livestreaming."></img>
                </Paper>
                :

                <div className={classes.videoContainer}>
                    <iframe title="LiveStream" src={"https://www.youtube-nocookie.com/embed/" + ytLiveUrlKey} frameborder="0" allowfullscreen className={classes.video}></iframe>
                </div>

            }
            <Metadata
                Title={LivePageMetadata.Title}
                Description={LivePageMetadata.Description}
            />
        </Layout>
    );
}
